import { Action } from '@ali/4ever-cangjie';
import { ITableSelection } from '../types';
import Table from '../../mo/models';

export const TABLE_SET_CELL_FILL = Symbol('TABLE_SET_CELL_FILL');

export function setTableCellFill(table, selection, fill) {
  return {
    type: TABLE_SET_CELL_FILL,
    payload: {
      node: table,
      selection,
      fill,
    },
  };
}

interface TableSetTableCellFillPayload {
  node: Table;
  selection: ITableSelection;
  fill: Record<string, any>;
}

interface TableSetTableCellFillAction {
  type: typeof TABLE_SET_CELL_FILL;
  payload: TableSetTableCellFillPayload;
}

export function isTableSetTableCellFillAction(
  action: Action,
): action is TableSetTableCellFillAction {
  return action.type === TABLE_SET_CELL_FILL;
}
